<template>
	<view class="container sureSale">
		<u-form :model="form" ref="uForm" label-position="top" border-bottom="true">
			<u-form-item label="销控结束时间设置" prop="endTime">
				<u-input v-model="form.endTime" />
			</u-form-item>
			<u-form-item label="备注" prop="memo">
				<u-input v-model="form.memo" />
			</u-form-item>
		</u-form>
		<u-button @click="submit">提交</u-button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				form: {
					name: '',
					memo: '',
				},
				rules: {
					name: [{
						required: true,
						message: '请输入姓名',
						// 可以单个或者同时写两个触发验证方式 
						trigger: ['change', 'blur'],
					}]
				}
			}
		},
		onReady() {
			this.$refs.uForm.setRules(this.rules);
		},
		methods: {
			submit() {
				this.$refs.uForm.validate(valid => {
					if (valid) {
						console.log('验证通过');
					} else {
						console.log('验证失败');
					}
				});
			}
		}
	}
</script>

<style lang="scss">
	.sureSale{
		padding: 30rpx;
	}
</style>
